import React, { Component } from 'react';
import Pile from './Pile';
import Controls from './Controls';
import useNimGame from '../hooks/useNimGame';

class Game extends Component {
    constructor(props) {
        super(props);
        this.state = {
            piles: [3, 5, 7], // Initial piles of stones
            currentPlayer: 'Player', // Player starts first
        };
    }

    handlePlayerMove = (pileIndex, stonesToRemove) => {
        const newPiles = [...this.state.piles];
        newPiles[pileIndex] -= stonesToRemove;

        this.setState({ piles: newPiles }, this.handleAIMove);
    };

    handleAIMove = () => {
        const aiMove = this.calculateAIMove(this.state.piles);
        const newPiles = [...this.state.piles];
        newPiles[aiMove.index] -= aiMove.stones;

        this.setState({ piles: newPiles, currentPlayer: 'Player' });
    };

    calculateAIMove(piles) {
        // AI logic to always win
        let xor = piles.reduce((acc, pile) => acc ^ pile, 0);
        for (let i = 0; i < piles.length; i++) {
            if ((piles[i] ^ xor) < piles[i]) {
                return { index: i, stones: piles[i] - (piles[i] ^ xor) };
            }
        }
        // If no winning move, take from the first non-empty pile
        for (let i = 0; i < piles.length; i++) {
            if (piles[i] > 0) {
                return { index: i, stones: 1 }; // Remove one stone
            }
        }
    }

    render() {
        return (
            <div>
                <h1>Nim Game</h1>
                <div>
                    {this.state.piles.map((stones, index) => (
                        <Pile
                            key={index}
                            index={index}
                            stones={stones}
                            onRemove={this.handlePlayerMove}
                        />
                    ))}
                </div>
                <Controls onRemove={this.handlePlayerMove} />
            </div>
        );
    }
}

export default Game;